import React from "react";
import { NAV_CONSTANTS } from "../const/constants";
import { selectedQuestionsIndexes, allQuestions, resetSelectedQuestionsIndexes } from "../database/database";
import { d } from "../debug";
import BackButton from "./BackButton";
import ActionButton from "./ActionButton";

class QuestionsDisplayer extends React.Component {

  constructor() {
    super();
    this.state = { currentQuestion: 0 }
  }

  toNextQuestion() {
    this.setState({ currentQuestion: this.state.currentQuestion < selectedQuestionsIndexes.length - 1 ? this.state.currentQuestion + 1 : this.state.currentQuestion })
  }

  toPreviousQuestion() {
    this.setState({ currentQuestion: this.state.currentQuestion > 0 ? this.state.currentQuestion - 1 : this.state.currentQuestion });
  }

  onHomeClick() {
    resetSelectedQuestionsIndexes();
    this.props.redirect(NAV_CONSTANTS.HOME);
  }

  onBackClick() {
    resetSelectedQuestionsIndexes();
  }

  render() {
    return (
      <React.Fragment>
        <BackButton redirect={this.props.redirect} target={NAV_CONSTANTS.HOME} secondaryAction={() => this.onBackClick()} />
        <p class="QD-question">{allQuestions[selectedQuestionsIndexes[this.state.currentQuestion]]}</p>
        <div class="QD-buttons">
          <ActionButton className="QD-button" text="Previous" action={() => this.toPreviousQuestion()} />
          <ActionButton className="QD-button" text="Next" action={() => this.toNextQuestion()} />
        </div>
        {this.state.currentQuestion == selectedQuestionsIndexes.length - 1 &&
          <ActionButton text="Home" action={() => this.onHomeClick()} />}
      </React.Fragment>
    );
  }
} //class

export default QuestionsDisplayer;