import React from "react";
import { NAV_CONSTANTS } from "../const/constants";
import BackButton from "./BackButton";
import { allQuestions, resetSelectedQuestionsIndexes } from "../database/database";
import Question from "./Question";

class QuestionsSelector extends React.Component {
  onBackClick() {
    resetSelectedQuestionsIndexes();
  }
  render() {
    return (
      <React.Fragment>
      <div className="app-header">
        <BackButton redirect={this.props.redirect} target={NAV_CONSTANTS.HOME} secondaryAction={()=> this.onBackClick()}/>
        <p>Select questions</p>
      </div>

        {allQuestions.map((item, i) => {
          return <Question questionIndex={i} />
        })}
        <button onClick={() => this.props.redirect(NAV_CONSTANTS.QUESTIONS_DISPLAYER)}>OK!</button>
      </React.Fragment>
    );
  }
} //class

export default QuestionsSelector;