import React from "react";
import { NAV_CONSTANTS } from "../const/constants";
import { selectRandomQuestions } from "../database/database";
import BackButton from "./BackButton";

const QUESTIONS_NUMBERS = [5, 10, 20, 30, 40, 50, 60];
class RandomQuestionsSelector extends React.Component {

  selectRandomNumberOfQuestions(numberOfQuestions) {
    selectRandomQuestions(numberOfQuestions);
    this.props.redirect(NAV_CONSTANTS.QUESTIONS_DISPLAYER);
  }

  render() {
    return (
      <div>
        <BackButton redirect={this.props.redirect} target={NAV_CONSTANTS.HOME} />
        <p className="RQS-title">How many random questions?</p>
        <div className="RQS-buttons">
          {QUESTIONS_NUMBERS.map((item) => {
            return  <button className="RQS-button" onClick={() => this.selectRandomNumberOfQuestions(item)}>{item}</button>;
          })}

        </div>
      </div>
    );
  }
}//class

export default RandomQuestionsSelector;