import React from "react";
import { NAV_CONSTANTS } from "../const/constants";
import ActionButton from "./ActionButton";

class HomeMenu extends React.Component {

  render() {
    return (
      <div className="home-menu-ctn">
        <div className="home-menu">
          <ActionButton className="button-home-menu" text={"Random questions"} action={this.props.redirect} params={NAV_CONSTANTS.RANDOM_QUESTIONS_SELECTOR} />
          <ActionButton className="button-home-menu" text={"Select questions"} action={this.props.redirect} params={NAV_CONSTANTS.QUESTIONS_SELECTOR} />
        </div>
      </div>
    );
  }
}//class

export default HomeMenu;