import React from "react";

class ActionButton extends React.Component {

  constructor() {
    super();
  }

  onButtonClicked() {
    setTimeout(()=>{
      if(this.props.params){
        this.props.action(this.props.params);
      }
      else {
        this.props.action();
      }
    }, 200) 
  }
  render() {
    return (
      <button className={this.props.className + " action-button"} onClick={() => this.onButtonClicked()}>
        {this.props.text}
      </button>
    );
  }
}//class

export default ActionButton;