import React from "react";

class BackButton extends React.Component {
  constructor(){
    super();
  }

  onBackButtonClick() {
    if(this.props.secondaryAction) {
      this.props.secondaryAction();
    }
    this.props.redirect(this.props.target);
  }

  render() {
    return (
      <button className="back-btn" onClick={()=> this.onBackButtonClick()}>
        <img className="back-btn-img" src="./img/arrow.png"/>
      </button>
    );
  }
}//class
  
export default BackButton;