import React from "react";
import HomeMenu from "./HomeMenu";
import QuestionsDisplayer from "./QuestionsDisplayer";
import QuestionsSelector from "./QuestionsSelector";
import RandomQuestionsSelector from "./RandomQuestionsSelector";
import { NAV_CONSTANTS } from "../const/constants";
class Home extends React.Component {

  constructor(){
    super();
    this.state = { navState: NAV_CONSTANTS.HOME };
  }
  redirect = (direction) => {
    this.setState({navState: direction});
  }

  render() {
    return (
      <React.Fragment >
        {this.state.navState == NAV_CONSTANTS.HOME && <HomeMenu redirect = {this.redirect}/>}
        {this.state.navState == NAV_CONSTANTS.RANDOM_QUESTIONS_SELECTOR && <RandomQuestionsSelector redirect = {this.redirect} />}
        {this.state.navState == NAV_CONSTANTS.QUESTIONS_SELECTOR && <QuestionsSelector redirect = {this.redirect}/>}
        {this.state.navState == NAV_CONSTANTS.QUESTIONS_DISPLAYER && <QuestionsDisplayer redirect = {this.redirect}/>}
      </React.Fragment>
    );
  }
} //class

export default Home;