import React from "react";
import { allQuestions, removeQuestion, selectQuestion } from "../database/database";

class Question extends React.Component {
  
  constructor(){
    super();
    this.state = {isSelected: false}
  }

  onQuestionSelected = () => {
    selectQuestion(this.props.questionIndex);
  }

  onQuestionUnselected = () => {
    removeQuestion(this.props.questionIndex);
  }

  toggleQuestionSelection = () =>{
    this.setState({isSelected: !this.state.isSelected});
    if(this.state.isSelected){
      this.onQuestionUnselected();
    }
    else{
      this.onQuestionSelected();
    }
  }

  render() {
    return (
      <div onClick={()=>this.toggleQuestionSelection()}>
        {this.state.isSelected && '✅' }
        {!this.state.isSelected && '⬜' }
        {allQuestions[this.props.questionIndex]}
      </div>
    );
  }
}//class
  
export default Question;