export let selectedQuestionsIndexes = [];

export const resetSelectedQuestionsIndexes = () => {
  selectedQuestionsIndexes = [];
  d("after resetting selectedQuestionsIndexes, ", selectedQuestionsIndexes);
}

/**
 * Returns a random number between min (inclusive) and max (exclusive)
 */
const getRandomArbitrary = (min, max) => {
  return Math.random() * (max - min) + min;
}

const generateValidRandomIndex = () => {
  let randomIndex = getRandomArbitrary(0, allQuestions.length);
  while (isIndexAlreadySelected(randomIndex)) {
    randomIndex = getRandomArbitrary(0, allQuestions.length);
  }
  return Math.round(randomIndex);
}

const isIndexAlreadySelected = (index) => {
  return selectedQuestionsIndexes.includes(index);
}

export const selectRandomQuestions = (numberOfQuestions) => {
  for (let index = 0; index < numberOfQuestions; index++) {
    let indexToGet = generateValidRandomIndex();
    selectedQuestionsIndexes.push(indexToGet);
  }
}

export const selectQuestion = (questionIndex) => {
  selectedQuestionsIndexes.push(questionIndex);
  d("selecting question, selected questions is now ", selectedQuestionsIndexes)
}

export const removeQuestion = (questionIndex) => {
  d("removing question, questionIndex = ", questionIndex)
  selectedQuestionsIndexes = selectedQuestionsIndexes.filter(index => index != questionIndex);
  d("removing question, selected questions is now ", selectedQuestionsIndexes)

}